import request from '@/utils/request'

/**
 *   body  data   query  params   headers  headers 
 */
export const getArticle = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/articles',
    params
  })
}

export const getChannels = () => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/channels'
  })
}
// :XX 
export const deleteArticle = id => {
  return request({
    method: 'DELETE',
    url: `/mp/v1_0/articles/${id}`
  })
}

export const publishArticle = (data, draft = false) => {
  return request({
    method: 'POST',
    url: '/mp/v1_0/articles',
    params: {
      draft
    },
    data
  })
}

export const updateArticle = (id, data, draft = false) => {
  return request({
    method: 'PUT',
    url: `/mp/v1_0/articles/${id}`,
    params: {
      draft
    },
    data
  })
}

export const getOneArticle = id => {
  return request({
    method: 'GET',
    url: `/mp/v1_0/articles/${id}`
  })
}

export const uploadImage = data => {
  return request({
    method: 'POST',
    url: '/mp/v1_0/user/images',
    //  content-type multipart/form-data
    // axiosdataformdata
    data
  })
}

export const getImages = pageData => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/user/images',
    params: pageData
  })
}

export const collectImage = (id, collect) => {
  return request({
    method: 'PUT',
    url: `/mp/v1_0/user/images/${id}`,
    data: { collect }
  })
}

export const deleteImage = id => {
  return request({
    method: 'DELETE',
    url: `/mp/v1_0/user/images/${id}`
  })
}

export const updateComment = (id, allowComment) => {
  return request({
    method: 'PUT',
    url: '/mp/v1_0/comments/status',
    params: {
      article_id: id
    },
    data: {
      allow_comment: allowComment
    }
  })
}
